<script setup>
import FinexArticle from './FinexArticle.vue'
import { addDoc, collection, Timestamp, orderBy, query } from 'firebase/firestore'
import { useCollection, useFirestore } from 'vuefire'
import { ref } from 'vue';
const db = useFirestore()

const props = defineProps({
  isAdmin: Boolean,
  Date: Timestamp.fromDate(new Date())
});

const queryValue = ref("");
const search = ref("");

const articles = useCollection(query(collection(db, 'Articles'), orderBy('Date')));

function addNewArticle() {
  addDoc(collection(useFirestore(), 'Articles'), {
    Enabled: false
  })
}

function searchValue() {
  search.value = queryValue.value;
}

function clearSearch() {
  search.value = "";
  queryValue.value = "";
}

</script>

<template>
    <main class="flex flex-col items-center m-6">
      <button class="bg-klu-blue text-white rounded-md px-3" v-if="isAdmin" @click="addNewArticle">Add new article</button>
      <div class="flex w-1/2  my-4">
        <input v-model="queryValue" class="border-2 border-klu-blue w-full"/>
        <button @click="searchValue" class="bg-klu-blue px-3 py-1 text-white rounded-tr-md rounded-br-md">search</button> 
      </div>
      <button v-if="search" @click="clearSearch" class="bg-red px-3 py-1 text-white rounded-md">clear search</button>
      <FinexArticle 
        v-for="article in articles" :key="article.id"
        :article="article"
        :isAdmin="props.isAdmin"
        :query="search"
        class="my-12"
      />
    </main>
</template>

